.profilePage {
  display: grid;
  margin-top: 3%;
  justify-content: center;
  align-items: center;
}

.profileCard {
  background-color: #60308b;
  justify-content: space-between;
  align-items: center;
  padding-left: 2rem;
  padding-right: 2rem;
  border-radius: 0.8rem;
  padding-top: 20px;
  padding-bottom: 20px;
}

.profileName {
  margin-top: 0.5rem;
  margin-left: 1rem;
  color: white;
  font-weight: bold;
  font-size: 1.2rem;
}

.editpProfileButton {
  color: white;
}
.editProfileImage {
  height: 3.4rem;
  width: 3.4rem;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 0.2rem;
  object-fit: cover;
}

ul {
  padding-left: 0;
}

.profileTabs {
  color: white;
  margin-top: 2rem;
  margin-bottom: 1rem;
}

.profileDialogHeader {
  margin-bottom: 0.8rem;
}

.profileImageContainer {
  margin-top: 0.5rem;
}

.MuiPaper-elevation4 {
  box-shadow: none;
}

.PrivateTabIndicator-colorSecondary-257,
.PrivateTabIndicator-colorSecondary-258,
.MuiTabs-indicator {
  background-color: white;
}
.MuiTab-textColorPrimary.Mui-selected {
  color: white;
}
.MuiTab-textColorPrimary {
  color: rgba(255, 255, 255, 0.5);
  font-weight: bold;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  font-size: 1rem;
}
.MuiAppBar-colorDefault {
  background-color: rgba(0, 0, 0, 0.001);
}
